<script>
export default {
  methods: {
    animLettre: function () {
      const p = document.querySelectorAll("p");
      p.forEach((e) => {
        const an = e.dataset.animation;
        e.addEventListener("animationend", () => e.classList.remove(an));
        e.addEventListener("mouseover", () => e.classList.add(an));
      });
    },
  },
};
